<template>
    <v-layout fill-height column class="router_width" justify-center>
      <v-form v-model="valid"  ref="form" class="d-flex" style="gap: 20px;">
        <v-layout column >
            <v-text-field
              v-model="name"
              color="#fff"
              dark
              label="이름"
              :rules="[rules.required]"
              outlined
              dense
            />
            <v-text-field
              v-model="phone"
              label="전화번호"
              dark
              :rules="[rules.required]"
              dense
              outlined
            />
            <v-text-field
              v-model="email"
              :rules="[rules.required,rules.emailRules]"
              dark
              outlined
              label="이메일"
              dense
            />
        </v-layout>
        <v-textarea
          label="내용"
          no-resize
          dark
          :rules="[rules.required]"
          outlined
          rows="6"
          v-model="message"
        />
      </v-form>
        <v-btn
          :disabled="!valid"
          :loading="isLoading"
          depressed
          large
          dark
          min-width="30%"
          max-width="30%"
          style="align-self: center;"
          @click="sendEmail"
        >
        Send Message
        </v-btn>
    </v-layout>
</template>


<script>
import emailjs from 'emailjs-com'
export default {
    components: {
      
    },
    props: {},
    data() {
        return {
          isLoading:false,
          valid:false,
          rules:{
            required :  v => !!v || '필수입니다.',
            emailRules : v => /.+@.+\..+/.test(v) || '올바른 이메일 양식이 아닙니다.',
          },
          phone:'',
          name:'',
          email:'',
          message:''
        };
    },
    watch: {
    },
    created() {},
    mounted() {
    },
    destroyed() {},
    methods: {
      sendEmail() {
        this.isLoading= true;
        const context = {
          name: this.name,
          email: this.email,
          message: this.message,
          phone : this.phone
        }
      try {
        emailjs.send('emfkrhwhem1', 'template_08m01br', context ,'bH7Te-6dSpEur83GY')
      } catch(error) {
          console.log({error})
      }
      this.isLoading= false;
      this.$refs.form.reset()
      },
    },
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/app.scss";

</style>